import { toDomPrecision } from '@tldraw/primitives'
import { TLShapeId } from '@tldraw/tlschema'
import { dedupe, modulate } from '@tldraw/utils'
import * as React from 'react'
import { track } from 'signia-react'
import {
	useApp,
	useCanvasEvents,
	useCoarsePointer,
	useDocumentEvents,
	useFixSafariDoubleTapZoomPencilEvents,
	useGestureEvents,
	useQuickReactor,
	useScreenBounds,
} from '../../hooks'
import { usePattern } from '../../hooks/usePattern'
import { debugFlags } from '../../utils/debug-flags'
import { Brush } from '../Brush'
import { Grid } from '../Grid'
import { HandlesContainer } from '../Handle'
import { LiveCollaborators } from '../LiveCollaborators'
import { Scribble } from '../Scribble'
import { SelectionBg, SelectionFg } from '../Selection'
import { Shape } from '../Shape'
import { ShapeIndicator } from '../ShapeIndicator'
import { SnapLines } from '../SnapLine/SnapLine'

const DupSvg = track(function DupSvg({ id }: { id: TLShapeId }) {
	const app = useApp()
	const shape = app.getShapeById(id)

	const [html, setHtml] = React.useState('')

	React.useEffect(() => {
		let cancelled = false
		;(async () => {
			if (cancelled) return
			const bb = app.getPageBoundsById(id)
			const el = await app.getSvg([id], { padding: 0 })
			if (el && bb && !cancelled) {
				el.style.setProperty('overflow', 'visible')
				el.setAttribute('preserveAspectRatio', 'xMidYMin slice')
				el.style.setProperty('transform', `translate(${bb.x}px, ${bb.y + bb.h + 12}px)`)
				el.style.setProperty('border', '1px solid black')
				setHtml(el?.outerHTML)
			}
		})()

		return () => {
			cancelled = true
		}
	}, [app, shape, id])
	return (
		<div style={{ paddingTop: 12, position: 'absolute' }}>
			<div style={{ display: 'flex' }} dangerouslySetInnerHTML={{ __html: html }} />
		</div>
	)
})

/** @public */
export const Canvas = track(function Canvas({
	onDropOverride,
}: {
	onDropOverride?: (
		defaultOnDrop: (e: React.DragEvent<Element>) => Promise<void>
	) => (e: React.DragEvent<Element>) => Promise<void>
}) {
	const app = useApp()

	const rCanvas = React.useRef<HTMLDivElement>(null)
	const rHtmlLayer = React.useRef<HTMLDivElement>(null)
	const rSvgLayer = React.useRef<SVGGElement>(null)

	useDocumentEvents()
	useCoarsePointer()
	useGestureEvents(rCanvas)
	useFixSafariDoubleTapZoomPencilEvents(rCanvas)
	useScreenBounds()

	useQuickReactor(
		'position layers',
		() => {
			const svgElm = rSvgLayer.current
			const htmlElm = rHtmlLayer.current
			if (!(svgElm && htmlElm)) return

			const { x, y, z } = app.camera

			// Because the html container has a width/height of 1px, we
			// need to create a small offset when zoomed to ensure that
			// the html container and svg container are lined up exactly.
			const offset =
				z >= 1 ? modulate(z, [1, 8], [0.125, 0.5], true) : modulate(z, [0.1, 1], [-2, 0.125], true)

			svgElm.style.setProperty(
				'transform',
				`scale(${toDomPrecision(z)}) translate(${toDomPrecision(x)}px,${toDomPrecision(y)}px)`
			)
			htmlElm.style.setProperty(
				'transform',
				`scale(${toDomPrecision(z)}) translate(${toDomPrecision(x + offset)}px,${toDomPrecision(
					y + offset
				)}px)`
			)
		},
		[app]
	)

	const { context: patternContext, isReady: patternIsReady } = usePattern()

	const events = useCanvasEvents()
	if (onDropOverride) {
		events.onDrop = onDropOverride(events.onDrop)
	}

	React.useEffect(() => {
		if (patternIsReady && app.isSafari) {
			const svgElm = rSvgLayer.current
			const htmlElm = rHtmlLayer.current

			if (svgElm && htmlElm) {
				// Wait for `patternContext` to be picked up
				requestAnimationFrame(() => {
					svgElm.style.display = 'none'
					htmlElm.style.display = 'none'

					// Wait for 'display = "none"' to take effect
					requestAnimationFrame(() => {
						svgElm.style.display = ''
						htmlElm.style.display = ''
					})
				})
			}
		}
	}, [app, patternIsReady])

	React.useEffect(() => {
		rCanvas.current?.focus()
	}, [])

	return (
		<div
			ref={rCanvas}
			tabIndex={-1}
			draggable={false}
			className="rs-canvas"
			data-wd="canvas"
			{...events}
		>
			<Grid size={app.gridSize} />
			<div ref={rHtmlLayer} className="rs-html-layer" draggable={false}>
				<SelectionBg />
				<ShapesToDisplay />
			</div>
			<svg className="rs-svg-layer">
				{patternContext}
				<defs>
					<Cursor />
					<CursorHint />
					<marker id="arrowhead-dot" className="rs-arrow-hint" refX="3.0" refY="3.0" orient="0">
						<circle cx="3" cy="3" r="2" strokeDasharray="100%" />
					</marker>
					<marker
						id="arrowhead-cross"
						className="rs-arrow-hint"
						refX="3.0"
						refY="3.0"
						orient="auto"
					>
						<line x1="1.5" y1="1.5" x2="4.5" y2="4.5" strokeDasharray="100%" />
						<line x1="1.5" y1="4.5" x2="4.5" y2="1.5" strokeDasharray="100%" />
					</marker>
				</defs>
				<g ref={rSvgLayer}>
					<LiveCollaborators />
					<Scribble />
					<Brush />
					<SelectedIdIndicators />
					<HoveredShapeIndicator />
					<SelectionFg />
					<HintedShapeIndicator />
					<HandlesContainer />
					<SnapLines />
				</g>
			</svg>
		</div>
	)
})

const ShapesToDisplay = track(function ShapesToDisplay() {
	const app = useApp()

	const { renderingShapes } = app

	const debugSvg = debugFlags.debugSvg.value
	if (debugSvg) {
		return (
			<>
				{renderingShapes.map((result) => (
					<React.Fragment key={result.id + '_fragment'}>
						<Shape key={result.id + '_shape'} {...result} />
						<DupSvg key={result.id + '_shape_debug'} id={result.id} />
					</React.Fragment>
				))}
			</>
		)
	}

	return (
		<>
			{renderingShapes.map((result) => (
				<Shape key={result.id + '_shape'} {...result} />
			))}
		</>
	)
})

const SelectedIdIndicators = track(function SelectedIdIndicators() {
	const app = useApp()

	const selectedIds = app.selectedIds

	const isChangingStyle = app.isChangingStyle

	const shouldDisplay =
		app.isInAny(
			'select.idle',
			'select.brushing',
			'select.scribble_brushing',
			'select.pointing_shape',
			'select.pointing_selection',
			'select.pointing_handle'
		) && !isChangingStyle

	if (!shouldDisplay) return null

	return (
		<g aria-label="SELECTED INDICATORS">
			{selectedIds.map((id) => (
				<ShapeIndicator key={id + '_indicator'} id={id} />
			))}
		</g>
	)
})

const HoveredShapeIndicator = track(function HoveredShapeIndicator() {
	const app = useApp()

	const id = app.pageState.hoveredId

	const shouldDisplay = app.isInAny('select.idle', 'select.editing_shape')

	if (!(id && shouldDisplay)) return null

	return <ShapeIndicator id={id} key={id} />
})

const HintedShapeIndicator = track(function HintedShapeIndicator() {
	const app = useApp()

	const ids = dedupe(app.hintingIds)

	if (!ids.length) return null

	return (
		<g aria-label="HINTED SHAPES">
			{ids.map((id) => (
				<ShapeIndicator id={id} key={id + '_hinting'} isHinting />
			))}
		</g>
	)
})

function Cursor() {
	return (
		<g id="cursor">
			<g fill="rgba(0,0,0,.2)" transform="translate(-11,-11)">
				<path d="m12 24.4219v-16.015l11.591 11.619h-6.781l-.411.124z" />
				<path d="m21.0845 25.0962-3.605 1.535-4.682-11.089 3.686-1.553z" />
			</g>
			<g fill="white" transform="translate(-12,-12)">
				<path d="m12 24.4219v-16.015l11.591 11.619h-6.781l-.411.124z" />
				<path d="m21.0845 25.0962-3.605 1.535-4.682-11.089 3.686-1.553z" />
			</g>
			<g fill="currentColor" transform="translate(-12,-12)">
				<path d="m19.751 24.4155-1.844.774-3.1-7.374 1.841-.775z" />
				<path d="m13 10.814v11.188l2.969-2.866.428-.139h4.768z" />
			</g>
		</g>
	)
}

// A triangle pointing right
function CursorHint() {
	return <path id="cursor_hint" fill="currentColor" d="M -2,-5 2,0 -2,5 Z" opacity=".8" />
}
