import classNames from 'classnames'
import { forwardRef } from 'react'
import { useAssetUrls } from '../../../hooks'
import { TLUiIconType } from '../../../icon-types'

/** @public */
export interface IconProps extends React.HTMLProps<HTMLDivElement> {
	icon: TLUiIconType
	small?: boolean
	children?: undefined
	invertIcon?: boolean
	crossOrigin?: 'anonymous' | 'use-credentials'
}

/** @public */
export const Icon = forwardRef<HTMLDivElement, IconProps>(function Icon(
	{ small, invertIcon, icon, className, ...props },
	ref
) {
	const assetUrls = useAssetUrls()

	return (
		<div
			ref={ref}
			{...props}
			className={classNames('tlui-icon', { 'tlui-icon__small': small }, className)}
			style={{
				mask: `url(${assetUrls.icons[icon]}) center 100% / 100% no-repeat`,
				WebkitMask: `url(${assetUrls.icons[icon]}) center 100% / 100% no-repeat`,
				backgroundColor: 'currentcolor',
				transform: invertIcon ? 'scale(-1, 1)' : undefined,
			}}
		/>
	)
})
