import { modulate } from '@tldraw/utils'
import { track } from 'signia-react'
import { GRID_STEPS } from '../../constants'
import { useApp } from '../../hooks'

export interface UiGridProps {
	size: number
}

export type UiGridComponent = (props: UiGridProps) => any | null

export const Grid = track(function Grid({ size }: UiGridProps) {
	const app = useApp()

	const { x, y, z } = app.camera
	const isGridMode = app.isGridMode

	if (!isGridMode) return null

	return (
		<svg className="tl-grid" version="1.1" xmlns="http://www.w3.org/2000/svg">
			<defs>
				{GRID_STEPS.map(({ min, mid, step }, i) => (
					<GridLayer
						key={`grid-layer-${i}`}
						min={min}
						mid={mid}
						step={step}
						x={x}
						y={y}
						z={z}
						size={size}
					/>
				))}
			</defs>
			{GRID_STEPS.map(({ step }, i) => (
				<rect key={`grid-rect-${i}`} width="100%" height="100%" fill={`url(#grid-${step})`} />
			))}
		</svg>
	)
})

function GridLayer({
	step,
	min,
	mid,
	size,
	x,
	y,
	z,
}: {
	step: number
	min: number
	mid: number
	size: number
	x: number
	y: number
	z: number
}) {
	const s = step * size * z
	const xo = 0.5 + x * z
	const yo = 0.5 + y * z
	const gxo = xo > 0 ? xo % s : s + (xo % s)
	const gyo = yo > 0 ? yo % s : s + (yo % s)
	const opacity = z < mid ? modulate(z, [min, mid], [0, 1]) : 1

	return (
		<pattern
			key={`grid-pattern-${step}`}
			id={`grid-${step}`}
			width={s}
			height={s}
			patternUnits="userSpaceOnUse"
		>
			<circle className={`tl-grid-dot`} cx={gxo} cy={gyo} r={1} opacity={opacity} />
		</pattern>
	)
}
