import { getStroke, getStrokePoints, Vec2d, VecLike } from '@tldraw/primitives'
import { TLGeoShape } from '@tldraw/tlschema'
import { rng } from '@tldraw/utils'
import * as React from 'react'
import { getSvgPathFromStroke, getSvgPathFromStrokePoints } from '../../../../utils'
import { getShapeFillSvg, getSvgWithShapeFill, ShapeFill, TLExportColors } from '../../shared'

export const DrawStylePolygon = React.memo(function DrawStylePolygon({
	id,
	bgOutline,
	outline,
	fill,
	color,
	strokeWidth,
}: Pick<TLGeoShape['props'], 'fill' | 'color'> & {
	id: TLGeoShape['id']
	outline: VecLike[]
	strokeWidth: number
	bgOutline?: VecLike[]
}) {
	const points = getPolygonDrawPoints(id, outline, strokeWidth)
	const options = getPolygonStrokeOptions(strokeWidth)
	const strokePoints = getStrokePoints(points, options)

	const innerPath = getSvgPathFromStrokePoints(
		bgOutline
			? getStrokePoints(getPolygonDrawPoints(id, bgOutline, strokeWidth), options)
			: strokePoints,
		false
	)
	const outerPath = getSvgPathFromStroke(getStroke(points, options))

	return (
		<>
			<ShapeFill d={innerPath} fill={fill} color={color} />
			<path d={outerPath} fill="currentColor" pointerEvents="all" />
		</>
	)
})

export function DrawStylePolygonSvg({
	id,
	outline,
	bgOutline,
	fill,
	color,
	colors,
	strokeWidth,
}: Pick<TLGeoShape['props'], 'fill' | 'color'> & {
	id: TLGeoShape['id']
	outline: VecLike[]
	bgOutline?: VecLike[]
	strokeWidth: number
	colors: TLExportColors
}) {
	const points = getPolygonDrawPoints(id, outline, strokeWidth)
	const options = getPolygonStrokeOptions(strokeWidth)

	const strokeElement = document.createElementNS('http://www.w3.org/2000/svg', 'path')
	strokeElement.setAttribute('d', getSvgPathFromStroke(getStroke(points, options), true))
	strokeElement.setAttribute('fill', colors.fill[color])

	// Get the fill element, if any
	const fillElement = getShapeFillSvg({
		d: getSvgPathFromStrokePoints(
			getStrokePoints(
				bgOutline ? getPolygonDrawPoints(id, bgOutline, strokeWidth) : points,
				options
			),
			false
		),
		fill,
		color,
		colors,
	})

	return getSvgWithShapeFill(strokeElement, fillElement)
}

export function getPolygonDrawPoints(id: string, outline: VecLike[], strokeWidth: number) {
	const points: Vec2d[] = []

	const getRandom = rng(id)

	const start = Math.round(Math.abs(getRandom()) * outline.length)

	const corners = outline.map((p) =>
		Vec2d.AddXY(p, (getRandom() * strokeWidth) / 4, (getRandom() * strokeWidth) / 4)
	)

	const len = corners.length

	for (let i = 0, n = len + 1; i < n; i++) {
		const At = corners[(start + i) % len]
		const Bt = corners[(start + i + 1) % len]

		const dist = Math.min(Vec2d.Dist(At, Bt) / 2, strokeWidth / 2)
		const A = Vec2d.Nudge(At, Bt, dist)

		const D = Vec2d.Med(At, Bt)

		if (i === 0) {
			Bt.z = 0.7
			points.push(new Vec2d(D.x, D.y, 0.7), Bt)
		} else if (i === outline.length) {
			const lastSegPoints = Vec2d.PointsBetween(A, D, 4)
			lastSegPoints.forEach((p) => (p.z = 0.7))
			points.push(...lastSegPoints)
		} else {
			points.push(...Vec2d.PointsBetween(A, Bt, 6))
		}
	}

	return points
}

export function getBoringPolygonDrawPoints(id: string, outline: VecLike[]) {
	const points: VecLike[] = []

	const getRandom = rng(id)
	for (let i = 0, n = outline.length; i < n; i++) {
		const A = outline[i]
		points.push(Vec2d.AddXY(A, getRandom() / 2, getRandom() / 2))
	}

	return points
}

export function getPolygonIndicatorPath(id: string, outline: VecLike[], strokeWidth: number) {
	const points = getPolygonDrawPoints(id, outline, strokeWidth)
	const options = getPolygonStrokeOptions(strokeWidth)
	const strokePoints = getStrokePoints(points, options)

	return getSvgPathFromStrokePoints(strokePoints, true)
}

function getPolygonStrokeOptions(strokeWidth: number) {
	return {
		size: 1 + strokeWidth * 0.618,
		last: true,
		simulatePressure: false,
		streamline: 0.25,
		thinning: 0.9,
	}
}
