import { VecLike } from '@tldraw/primitives'
import { TLGeoShape } from '@tldraw/tlschema'
import * as React from 'react'
import { getShapeFillSvg, getSvgWithShapeFill, ShapeFill, TLExportColors } from '../../shared'

export const SolidStylePolygon = React.memo(function SolidStylePolygon({
	outline,
	bgOutline,
	fill,
	color,
	strokeWidth,
}: Pick<TLGeoShape['props'], 'fill' | 'color'> & {
	outline: VecLike[]
	bgOutline?: VecLike[]
	strokeWidth: number
}) {
	const path = 'M' + outline[0] + 'L' + outline.slice(1) + 'Z'
	return (
		<>
			<ShapeFill
				d={bgOutline ? 'M' + bgOutline[0] + 'L' + bgOutline.slice(1) + 'Z' : path}
				fill={fill}
				color={color}
			/>
			<path d={path} stroke={`var(--palette-${color}`} strokeWidth={strokeWidth} fill="none" />
		</>
	)
})

export function SolidStylePolygonSvg({
	outline,
	bgOutline,
	fill,
	color,
	strokeWidth,
	colors,
}: Pick<TLGeoShape['props'], 'fill' | 'color'> & {
	outline: VecLike[]
	strokeWidth: number
	colors: TLExportColors
	bgOutline?: VecLike[]
}) {
	const strokeElementPathData = 'M' + outline[0] + 'L' + outline.slice(1) + 'Z'

	const strokeElement = document.createElementNS('http://www.w3.org/2000/svg', 'path')
	strokeElement.setAttribute('d', strokeElementPathData)
	strokeElement.setAttribute('stroke-width', strokeWidth.toString())
	strokeElement.setAttribute('stroke', colors.fill[color])
	strokeElement.setAttribute('fill', 'none')

	const fillPathData = bgOutline
		? 'M' + bgOutline[0] + 'L' + bgOutline.slice(1) + 'Z'
		: strokeElementPathData

	// Get the fill element, if any
	const fillElement = getShapeFillSvg({
		d: fillPathData,
		fill,
		color,
		colors,
	})

	return getSvgWithShapeFill(strokeElement, fillElement)
}
