import { EASINGS, getStroke } from '@tldraw/primitives'
import { track } from 'signia-react'
import { useApp } from '../../hooks'
import { getSvgPathFromStroke } from '../../utils'

export const Scribble = track(function Scribble() {
	const app = useApp()
	const scribble = app.scribble
	const zoom = app.zoomLevel

	if (!scribble) return null

	const d = getSvgPathFromStroke(
		getStroke(scribble.points, {
			size: scribble.size / zoom,
			start: { taper: true, easing: EASINGS.linear },
			last: false,
			simulatePressure: false,
		})
	)

	return (
		<path
			className="tl-scribble"
			d={d}
			fill={`var(--color-${scribble.color})`}
			opacity={scribble.opacity}
		/>
	)
})
