import { Vec2dModel } from '@tldraw/tlschema'
import { memo } from 'react'

export interface UiCursorProps {
	point: Vec2dModel | null
	zoom: number
	color?: string
}

export type UiCursorComponent = (props: UiCursorProps) => any | null

const _Cursor = ({ zoom, point, color }: UiCursorProps) => {
	if (!point) return null

	return (
		<use
			href="#cursor"
			transform={`translate(${point.x}, ${point.y}) scale(${1 / zoom})`}
			color={color}
		/>
	)
}

export const Cursor = memo(_Cursor)
