import classNames from 'classnames'
import { useSelectionEvents } from '../../../hooks/useSelectionEvents'

interface RotateHandleProps {
	cx: number
	cy: number
	size: number
	isHidden: boolean
	'data-wd'?: string
}

const SQUARE_ROOT_PI = Math.sqrt(Math.PI)

export const MobileRotateHandle = function RotateHandle({
	cx,
	cy,
	size,
	isHidden,
	'data-wd': dataWd,
}: RotateHandleProps) {
	const events = useSelectionEvents('mobile_rotate')

	return (
		<g>
			<circle
				data-wd={dataWd}
				className={classNames('rs-transparent', 'rs-mobile-rotate__bg', { 'rs-hidden': isHidden })}
				cx={cx}
				cy={cy}
				{...events}
			/>
			<circle
				className={classNames('rs-mobile-rotate__fg', { 'rs-hidden': isHidden })}
				cx={cx}
				cy={cy}
				r={size / SQUARE_ROOT_PI}
			/>
		</g>
	)
}
