import { toDomPrecision } from '@tldraw/primitives'
import { track } from 'signia-react'
import { useApp } from '../../hooks'

export const Brush = track(function Brush() {
	const app = useApp()
	const brush = app.brush

	const shouldDisplay = app.isIn('select.brushing')

	if (!brush || !shouldDisplay) return null

	return (
		<rect
			className="rs-brush"
			width={toDomPrecision(Math.max(1, brush.w))}
			height={toDomPrecision(Math.max(1, brush.h))}
			transform={`translate(${brush.x},${brush.y})`}
		/>
	)
})
