import { useCursor, useDarkMode, useForceUpdate, useZoomCss } from '../../hooks'
import { useSafariFocusOutFix } from '../../hooks/useSafariFocusOutFix'

/** @public */
export const Layout = ({ children }: { children: any }) => {
	useZoomCss()
	useCursor()
	useDarkMode()
	useForceUpdate()
	useSafariFocusOutFix()

	return children
}
