/* eslint-disable react-hooks/rules-of-hooks */
import { toDomPrecision } from '@tldraw/primitives'
import { imageShapeMigrations, imageShapeTypeValidator, TLImageShape } from '@tldraw/tlschema'
import { useEffect, useState } from 'react'
import { HTMLContainer } from '../../../components/HTMLContainer'
import { Spinner } from '../../../components/Spinner'
import { defineShape } from '../../../config/TLShapeDefinition'
import { usePrefersReducedMotion } from '../../../utils'
import { HyperlinkButton } from '../shared/HyperlinkButton'
import { TLBoxUtil } from '../TLBoxUtil'

const loadImage = async (url: string): Promise<HTMLImageElement> => {
	return new Promise((resolve, reject) => {
		const image = new Image()
		image.onload = () => resolve(image)
		image.onerror = () => reject(new Error('Failed to load image'))
		image.crossOrigin = 'anonymous'
		image.src = url
	})
}

const getStateFrame = async (url: string) => {
	const image = await loadImage(url)

	const canvas = document.createElement('canvas')
	canvas.width = image.width
	canvas.height = image.height

	const ctx = canvas.getContext('2d')
	if (!ctx) return

	ctx.drawImage(image, 0, 0)
	return canvas.toDataURL()
}

async function getDataURIFromURL(url: string): Promise<string> {
	const response = await fetch(url)
	const blob = await response.blob()
	return new Promise((resolve, reject) => {
		const reader = new FileReader()
		reader.onloadend = () => resolve(reader.result as string)
		reader.onerror = reject
		reader.readAsDataURL(blob)
	})
}

/** @public */
export class TLImageUtil extends TLBoxUtil<TLImageShape> {
	static type = 'image'

	override isAspectRatioLocked = () => true

	override defaultProps(): TLImageShape['props'] {
		return {
			opacity: '1',
			w: 100,
			h: 100,
			assetId: null,
			playing: true,
			url: '',
		}
	}

	render(shape: TLImageShape) {
		const [staticFrameSrc, setStaticFrameSrc] = useState('')

		const prefersReducedMotion = usePrefersReducedMotion()
		const asset = shape.props.assetId ? this.app.getAssetById(shape.props.assetId) : undefined
		const { w, h } = shape.props

		useEffect(() => {
			if (asset?.props.src && 'mimeType' in asset.props && asset?.props.mimeType === 'image/gif') {
				let cancelled = false
				const run = async () => {
					const newStaticFrame = await getStateFrame(asset.props.src!)
					if (cancelled) return
					if (newStaticFrame) {
						setStaticFrameSrc(newStaticFrame)
					}
				}
				run()

				return () => {
					cancelled = true
				}
			}
		}, [prefersReducedMotion, asset?.props])

		if (asset?.type === 'bookmark') {
			throw Error("Bookmark assets can't be rendered as images")
		}

		// We only want to reduce motion for mimeTypes that have motion
		const reduceMotion =
			prefersReducedMotion &&
			(asset?.props.mimeType?.includes('video') || asset?.props.mimeType?.includes('gif'))

		return (
			<>
				<HTMLContainer id={shape.id}>
					<div className="rs-image-container">
						{asset?.props.src ? (
							<div
								className={`rs-image rs-image-${shape.id}`}
								style={{
									backgroundImage: `url(${
										!shape.props.playing || reduceMotion ? staticFrameSrc : asset.props.src
									}`,
								}}
								draggable={false}
							/>
						) : (
							<Spinner x={(w - 38) / 2} y={(h - 38) / 2} />
						)}
						{asset?.props.isAnimated && !shape.props.playing && (
							<div className="rs-image__meda-tag">GIF</div>
						)}
					</div>
				</HTMLContainer>
				{'url' in shape.props && shape.props.url && <HyperlinkButton url={shape.props.url} />}
			</>
		)
	}

	indicator(shape: TLImageShape) {
		return <rect width={toDomPrecision(shape.props.w)} height={toDomPrecision(shape.props.h)} />
	}

	async toSvg(shape: TLImageShape) {
		const g = document.createElementNS('http://www.w3.org/2000/svg', 'g')
		const asset = shape.props.assetId ? this.app.getAssetById(shape.props.assetId) : null

		let src = asset?.props.src || ''
		if (src && src.startsWith('http')) {
			// If it's a remote image, we need to fetch it and convert it to a data URI
			src = (await getDataURIFromURL(src)) || ''
		}

		const image = document.createElementNS('http://www.w3.org/2000/svg', 'image')
		image.setAttributeNS('http://www.w3.org/1999/xlink', 'href', src)
		image.setAttribute('width', shape.props.w.toString())
		image.setAttribute('height', shape.props.h.toString())
		g.appendChild(image)

		return g
	}

	onDoubleClick = (shape: TLImageShape) => {
		const asset = shape.props.assetId ? this.app.getAssetById(shape.props.assetId) : undefined

		if (!asset) return

		const canPlay =
			asset.props.src && 'mimeType' in asset.props && asset.props.mimeType === 'image/gif'

		if (!canPlay) return

		this.app.updateShapes([
			{
				type: 'image',
				id: shape.id,
				props: {
					playing: !shape.props.playing,
				},
			},
		])
	}
}

/** @public */
export const TLImageShapeDef = defineShape<TLImageShape, TLImageUtil>({
	type: 'image',
	getShapeUtil: () => TLImageUtil,
	validator: imageShapeTypeValidator,
	migrations: imageShapeMigrations,
})
