import { TLHandle, TLShapeId } from '@tldraw/tlschema'
import classNames from 'classnames'
import { useHandleEvents } from '../../hooks'

export interface UiHandleProps<H extends TLHandle = TLHandle> {
	id: TLShapeId
	handle: H
}

export type UiHandleComponent<H extends TLHandle = TLHandle> = (
	props: UiHandleProps<H>
) => any | null

export function Handle<H extends TLHandle>({ id, handle }: UiHandleProps<H>) {
	const events = useHandleEvents(id, handle.id)

	const { x, y, type } = handle

	return (
		<g
			className={classNames('rs-handle', { 'rs-handle-hint': type !== 'vertex' })}
			aria-label="handle"
			transform={`translate(${x}, ${y})`}
			{...events}
		>
			<circle className="rs-handle-bg" />
			<circle className="rs-handle-fg" />
		</g>
	)
}
